"use client";

import React from "react";

interface NoteSettingsProps {
  color: string;
  onColorChange: (color: string) => void;
  passwordProtected: boolean;
  onTogglePassword: () => void;
  onDelete: () => void;
  isOpen: boolean;
  onClose: () => void;
}

export default function NoteSettings({
  color,
  onColorChange,
  passwordProtected,
  onTogglePassword,
  onDelete,
  isOpen,
  onClose,
}: NoteSettingsProps) {
  if (!isOpen) return null;

  return (
    <div className="absolute bottom-0 left-0 right-0 bg-gray-900 rounded-b-lg p-4 text-white">
      <div className="flex flex-col gap-3">
        <div className="flex items-center justify-between">
          <span className="text-sm">Border Color</span>
          <input
            type="color"
            value={color}
            onChange={(e) => onColorChange(e.target.value)}
            className="cursor-pointer w-8 h-8 p-0 border-0 rounded bg-transparent"
          />
        </div>
        <div className="flex items-center justify-between">
          <span className="text-sm">Password Protection</span>
          <button
            onClick={onTogglePassword}
            className="px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700 text-sm"
          >
            {passwordProtected ? "Disable" : "Enable"}
          </button>
        </div>
        <div className="flex items-center justify-between">
          <span className="text-sm">Delete Note</span>
          <button
            onClick={onDelete}
            className="px-3 py-1 bg-red-600 text-white rounded hover:bg-red-700 text-sm"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  );
}
