"use client";

import React, { useState } from "react";

interface Note {
  id: string;
  content: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
}

interface PasswordModalProps {
  note: Note;
  mode: "unlock" | "set";
  onClose: () => void;
  onUnlock?: (id: string, password: string) => void;
  onSetPassword?: (id: string, password: string) => void;
}

export default function PasswordModal({
  note,
  mode,
  onClose,
  onUnlock,
  onSetPassword,
}: PasswordModalProps) {
  const [inputPassword, setInputPassword] = React.useState("");

  const handleConfirm = () => {
    if (mode === "unlock" && onUnlock) {
      onUnlock(note.id, inputPassword);
    } else if (mode === "set" && onSetPassword) {
      onSetPassword(note.id, inputPassword);
    }
    setInputPassword("");
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-[9999]">
      <div className="bg-white rounded p-6 w-80 max-w-full text-black">
        <h2 className="text-lg font-semibold mb-4">
          {mode === "unlock" ? "Unlock Note" : "Set Password"}
        </h2>
        <input
          type="password"
          value={inputPassword}
          onChange={(e) => setInputPassword(e.target.value)}
          placeholder={mode === "unlock" ? "Enter password" : "Enter new password"}
          className="w-full p-2 border border-gray-300 rounded mb-4 bg-white text-black"
        />
        <div className="flex justify-end space-x-2">
          <button
            onClick={onClose}
            className="px-4 py-2 bg-gray-300 text-black rounded hover:bg-gray-400"
          >
            Cancel
          </button>
          <button
            onClick={handleConfirm}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            {mode === "unlock" ? "Unlock" : "Set Password"}
          </button>
        </div>
      </div>
    </div>
  );
}
